     'EINLEITUNG': `EINLEITUNG`,
     'Mitder ': 'Mit der folgenden DatenschutzerklÃ¤rung mÃ¶chten wir, die Bakuli GmbH, Am Steinberg 13, 01309 Dresden, Sie darÃ¼ber aufklÃ¤ren, welche Arten Ihrer personenbezogenen Daten (nachfolgend auch kurz als âDatenâ bezeichnet) wir zu welchen Zwecken und in welchem Umfang verarbeiten. Die DatenschutzerklÃ¤rung gilt fÃ¼r alle von uns durchgefÃ¼hrten Verarbeitungen personenbezogener Daten, sowohl im Rahmen der Erbringung unserer Leistungen als auch insbesondere auf unseren Webseiten, in mobilen Applikationen sowie innerhalb externer OnlineprÃ¤senzen, wie z.B. unserer Social-Media-Profile (nachfolgend zusammenfassend bezeichnet als âOnlineangebotâ). Die folgenden Bestimmungen informieren Sie Ã¼ber Art, Umfang, Dauer und Zweck der Verarbeitung personenbezogener Daten durch uns als verantwortlicher Anbieter dieser Website sowie Ã¼ber die von uns zu Optimierungszwecken sowie zur Steigerung der NutzungsqualitÃ¤t eingesetzten, datenschutzrelevanten Fremdkomponenten. Wir haben zahlreiche technische, vertragliche und organisatorische MaÃnahmen getroffen, um sicher zu stellen, dass die geltenden datenschutzrechtlichen Vorschriften eingehalten werden und um damit die durch uns verwalteten Daten vor zufÃ¤lliger oder vorsÃ¤tzlicher Manipulation, Verlust, ZerstÃ¶rung oder gegen den Zugriff unberechtigter Personen zu schÃ¼tzen. Bitte beachten Sie dennoch, dass internetbasierte DatenÃ¼bertragungen (z.B. die Kommunikation per E-Mail) grundsÃ¤tzlich SicherheitslÃ¼cken aufweisen, sodass ein absoluter Schutz nicht gewÃ¤hrleistet werden kann. Aus diesem Grund steht es jeder betroffenen Person frei, personenbezogene Daten auch auf alternativen Wegen, beispielsweise telefonisch, an uns zu Ã¼bermitteln. Die verwendeten Begriffe sind nicht geschlechtsspezifisch.',
