     'Die': 'Die nachfolgende Ãbersicht fasst die Arten der verarbeiteten Daten und die Zwecke ihrer Verarbeitung zusammen und verweist auf die betroffenen Personen.',
     // header ARTEN DER VERARBEITETEN DATEN
     'ARTENH1': 'ARTEN DER VERARBEITETEN DATEN',
     'Price': `â¢ Bestandsdaten (z.B. Namen, Adressen). â¢ Inhaltsdaten (z.B. Eingaben in Onlineformularen). â¢ Kontaktdaten (z.B. E-Mail, Telefonnummern). â¢ Meta-/Kommunikationsdaten (z.B. GerÃ¤te-Informationen, IP-Adressen). â¢ Nutzungsdaten (z.B. besuchte Webseiten, Interesse an Inhalten, Zugriffszeiten)`,
